<template>
  <v-app>
    <v-main style="margin-top: 15px">
      <v-container>
        <v-row>
          <v-col>
            <v-menu 
              v-model="datepickerOpened"
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              min-width="auto">
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  v-model="formattedRange"
                  label="Date range to show"
                  prepend-icon="mdi-calendar"
                  v-bind="attrs"
                  v-on="on"
                  readonly></v-text-field>
              </template>
              <v-date-picker
                v-model="computeWeekRange"
                first-day-of-week="1"
                @input="datepickerOpened = false"
                range></v-date-picker>
            </v-menu>
          </v-col>

          <v-col>
            <v-text-field 
              style="margin-start: 15px"
              v-model="updateRequestData"
              label="Request data"></v-text-field>
          </v-col>

          <v-col>
            <v-text-field 
              style="margin-start: 15px"
              v-model="updateResponseData"
              label="Response data"></v-text-field>
          </v-col>

          <v-col>
            <v-btn @click.end="selectNextWeek" style="margin-end: 15px">
              <v-icon>mdi-chevron-double-left</v-icon>
            </v-btn>
            <v-btn @click.end="selectPreviousDay" style="margin-end: 15px">
              <v-icon>mdi-arrow-left</v-icon>
            </v-btn>
            <v-btn @click.end="selectTodayWeek" style="margin-end: 15px">
              Today
            </v-btn>
            <v-btn @click.end="selectNextDay" style="margin-end: 15px">
              <v-icon>mdi-arrow-right</v-icon>
            </v-btn>
            <v-btn @click.end="selectPreviousWeek">
              <v-icon>mdi-chevron-double-right</v-icon>
            </v-btn>
          </v-col>
        </v-row>
        <v-calendar 
          color="primary" 
          type="week" 
          :start="dateToString(rangeStart)" 
          short-months="true" 
          :weekdays="weekdaysOrder" 
          :events="events"></v-calendar>
        </v-container>
    </v-main>
  </v-app>
</template>

<script>
export default {
  name: 'App',

  data: function(){
    return {
        workingDayColor: 'orange',
        periodColor: 'blue',
        freeTimeSlotColor: 'green',
        colors: ['indigo', 'deep-purple', 'cyan', 'darkCyan', 'grey darken-1'],
        findSlotsReq: {},
        findSlotsResp: [],
        datepickerOpened: false,
        rangeStart: new Date(), // fixme something wrong with initialized page and today button
    };
  },

  computed: {
    events: {
      get() {
        if (Object.keys(this.findSlotsReq).length < 1) {
          return [];
        }
        
        let res = [{
          name: "Search period",
          start: this.findSlotsReq.periodStart, 
          color: this.periodColor, 
          timed: true, 
          end: this.findSlotsReq.periodEnd, 
          category: "period",
        }]

        let currentDay = new Date(this.findSlotsReq.periodStart.getTime());
        while (this.findSlotsReq.periodEnd.getTime() - currentDay.getTime() > -24 * 60 * 60 * 1000) {
          res.push({
            name: "working day",
            color: this.workingDayColor,
            timed: true,
            start: new Date(this.dateToString(currentDay) + 'T' + this.findSlotsReq.dayStart + '+03:00'),
            end: new Date(this.dateToString(currentDay) + 'T' + this.findSlotsReq.dayEnd + '+03:00'),
            category: "working day",
          })
          currentDay.setTime(currentDay.getTime() + 24 * 60 * 60 * 1000); // plus one day
        }
        
        for (let i = 0; i < this.findSlotsReq.users.length; i++) {
          let user = this.findSlotsReq.users[i];
          for (let j = 0; j < user.availability.length; j++) {
            let availableTimeSlot = user.availability[j];
            res.push({
              name: user.email + " #" + j + " available slot",
              color: this.colors[i % this.colors.length],
              start: availableTimeSlot.start,
              end: availableTimeSlot.end,
              timed: true,
              category: user.email,
            })
          }
        }

        for (let i = 0; i < this.findSlotsResp.length; i++) {
          res.push({
            name: "free time slot from response",
              color: this.freeTimeSlotColor,
              start: this.findSlotsResp[i].start,
              end: this.findSlotsResp[i].end,
              timed: true,
              category: "free time slots",
          });
        }
        return res;
      }
    },
    formattedRange: {
      get() {
        return this.dateToString(this.rangeStart) + " - " + this.dateToString(this.addDays(this.rangeStart, 6))
      }
    },
    computeWeekRange: {
      get() {
        return [this.dateToString(this.rangeStart), this.dateToString(this.addDays(this.rangeStart, 6))];
      },
      set([startOfRangeStr]) {
        this.rangeStart = new Date(startOfRangeStr);
      },
    },
    weekdaysOrder: {
      get() {
        let newWeekdaysOrder = [];
        let startWeekday = this.rangeStart.getDay();
        for (let i = 0; i < 7; i++) {
          newWeekdaysOrder.push((startWeekday + i) % 7);
        }
        return newWeekdaysOrder;
      }
    },
    updateResponseData: {
      get() {
        return JSON.stringify(this.findSlotsResp);
      },
      set(responseData) {
        let resp = JSON.parse(responseData);
        let respTimeSlots = [];
        for (let i = 0; i < resp.length; i++) {
          respTimeSlots.push({
            start: new Date(resp[i].start),
            end: new Date(resp[i].end)
          });
        }
        this.findSlotsResp = respTimeSlots;
      }
    },
    updateRequestData: {
      get() {
        return JSON.stringify(this.findSlotsReq);
      },
      set(requestData) {
        let req = JSON.parse(requestData)
        this.findSlotsReq = {
          periodStart: new Date(req.period_start),
          periodEnd: new Date(req.period_end),
          dayEnd: req.day_end,
          dayStart: req.day_start,
          users: [],
        };

        let users = [];

        for (let i = 0; i < req.users.length; i++) {
          let user = req.users[i]
          let availabilityTimeSlots = [];
          for (let j = 0; j < user.availability.length; j++) {
            availabilityTimeSlots.push({
              start: new Date(user.availability[j].start),
              end: new Date(user.availability[j].end),
            });
          }
          users.push({
            availability: availabilityTimeSlots,
            email: user.email,
            icals: user.icals,
          });
        }
        this.findSlotsReq.users = users;
      }
    }
  },

  methods: {
      dateToString(date) {
        if (typeof(date) === 'string') {
          return ""
        }
        if (date === undefined || date === null) {
          return ""
        }
        return date.toISOString().slice(0, 10)
      },
      durationInMinutes(startStr, endStr) {
        let diffTime = Math.max(0, new Date(startStr) - new Date(endStr))
        return diffTime / (24 * 60);
      },
      addDays(date, days) {
        const newDate = new Date(date.getTime());
        return new Date(newDate.setDate(date.getDate() + days))
      }, 
      selectPreviousDay() {
        this.rangeStart = this.addDays(this.rangeStart, -1);
      },
      selectNextDay() {
        this.rangeStart = this.addDays(this.rangeStart, +1);
      },
      selectNextWeek() {
        this.rangeStart = this.addDays(this.rangeStart, -7);
      },
      selectPreviousWeek() {
        this.rangeStart = this.addDays(this.rangeStart, +7);
      },
      selectTodayWeek() {
        this.rangeStart = new Date();
      }
  },
};
</script>
